import 'object-defineproperty-ie'
import {get_search,get_limit} from './api/api.js'
//选择框逻辑
import {choose} from './choose.js';
// 患者画像
import {portrait} from './portrait.js';
// 医保规则查询
import {rules} from './rules.js'
//单独的键盘事件
import {key_down} from './key/key_down.js'

var search = $('.contant_search_pointer');
search.click(click)
$.ajax({
	url:get_limit(),
	datatype:'JSON',
	success:function(data){
		var dom = $('.contant_card')
		var contant = $('.contant')
		dom.empty();
		var str = '';
		for(var i = 0;i<data.length;i++){
			if(i == 0){
				str += `<li class="contant_card_list contant_card_list_active">${data[i].name}</li>`
			}else{
				str += `<li class="contant_card_list">${data[i].name}</li>`
			}
		}
		str +=
		`
		<li class="clear"></li>
		`
		dom.append(str)
		// portrait()
		choose()
		rules()
		key_down();
	},
	error:function(){
		alert('认证错误')
	}
})
export function click(e){
	e.preventDefault();
	var index = 0;
	var input = $('.contant_search_input').val();
	var list = $('.contant_search_list')
	list.off('click').on('click','li',function(){
		var more = $($('.contant_search_more')[$(this).index()])
		var text = $($('.contant_search_text')[$(this).index()])
		var other_text = $(this).siblings()
		for(var i = 0;i<other_text.length;i++){
			var a = $(other_text[i]).children()
			$(a[2]).text('更多 >')
			$(a[4]).css({'display':'none'})
		}
		if(more.text() == '更多 >'){
			more.text('收起 v')
			text.css({'display':'block'})
		}else{
			more.text('更多 >')
			text.css({'display':'none'})
		}
	})
	jQuery.support.cors=true
	$.ajax({
		url:get_search(),
		data:{
			word:input
		},
		datatype:'JSON',
		success:function(data){
			list.empty();
			if(data.status == 200){
				var str = ''
				for(var i = 0;i<data.name.length;i++){
					var val = '更多 >'
					var style = "style='display:none'"
					if(i == 0){
						val = '收起 v'
						style = ''
					}
					str+=
					"<li>"+
						"<div class='contant_search_img'></div>"+
						"<span class='contant_search_name'>"+
							data.name[i]+
						"</span>"+
						"<span class='contant_search_more'>"+
							val +
						"</span>"+
						"<div class='clear'></div>"+
						"<div class='contant_search_text' "+ style +">"+
							data.data[i]+
						"</div>"+
					"</li>"
				}
				list.append(str)
				var img_color = $('.contant_search_img')
				for(var i = 0;i<img_color.length;i++){
					$(img_color[i]).css({'background-position':16.7*(index%7) + '% 0'})
					index++;
				}
			}else if(data.status == 404){
				var str = ''
					str+=
					"<li>未搜索到相关数据</li>"
				list.append(str)
			}
		},
		error:function(){
			alert('错误')
		}
	})
}