export function table4type10(data){
	var str_table = '';
	var str_table_start = `
		<table class="contant_rules_table"  cellspacing=0>
			<thead>
				<tr>
					<td>药品</td>
					<td>开始年龄</td>
					<td>结束年龄</td>
					<td>年龄类型</td>
					<td>提醒依据(参考数据)</td>
					<td>数据来源</td>
				</tr>
			</thead>
			<tbody>`
	var table_mid = '';
	for(var i = 0;i<data.info.length;i++){
		table_mid += 
		`<tr>
			<td>${data.info[i][0]}</td>
			<td>${data.info[i][1]}</td>
			<td>${data.info[i][2]}</td>
			<td>${data.info[i][3]}</td>
			<td>${data.info[i][4]}</td>
			<td>${data.info[i][5]}</td>
		</tr>`
	}
				
	var table_end = 	
			`</tbody>
		</table>
	`;
	str_table = str_table_start + table_mid + table_end;
	return str_table;
}