/**
 * 设置查询条件的搜索框数量，根据参数进行动态的渲染
 * @param {Array} param			需要返回的参数，也是搜索框的个数
 * return						返回需要渲染的html标签，然后利用jquery来添加
 */
export function set_params(param){
	var str = ''
	var str_mid = '';
	var str_start = `
		<div class="contant_rules_everybox">`;
	var str_end =
	`</div>
	<div class="contant_rules_search">
		<span>查询</span>	
	</div>
	<div class="clear"></div>
	`
	for(var i = 0;i<param.length;i++){
		if(param[i] == 'drug'){
			str_mid +=  `
			<div class="contant_rules_clues">
				<div>
					<input type="text" >
					<span>药品 : </span>
				</div>
			</div>`
		}else if(param[i] == 'disease'){
			str_mid +=
			`<div class="contant_rules_clues">
				<div>
					<input type="text" >
					<span>病种 : </span>
				</div>
			</div>`
		}else if(param[i] == 'cure'){
			str_mid +=
			`<div class="contant_rules_clues">
				<div>
					<input type="text">
					<span>诊疗项目 : </span>
				</div>
			</div>`
		}else if(param[i] == 'drug_B'){
			str_mid +=  `
			<div class="contant_rules_clues">
				<div>
					<input type="text" >
					<span>药品B : </span>
				</div>
			</div>`
		}else if(param[i] == 'drug_A'){
			str_mid +=  `
			<div class="contant_rules_clues">
				<div>
					<input type="text" >
					<span>药品A : </span>
				</div>
			</div>`
		}
		
	};
	str = str_start + str_mid + str_end
	return str;
}
/**
 * 用于处理表格里面的每一项数据
 * @param {Array}	obj		数据的数组info
 * return 					返回已经经过处理的数组对象，改变原来数组
 */
export function null2str(obj){
	let re_obj = obj;
	for(var i = 0;i<obj.length;i++){
		if(obj[i] == null){
			obj[i] = '暂无'
		}
	}
	return re_obj
}