// 此文件为患者画像的具体逻辑
import { get_portrait} from './api/api.js'
export function portrait(){
	var dom = $('.contant_pic')
	ajax(dom)
	setInterval(function(){
		ajax(dom)
	},5000)
}
function ajax(dom){
	var doc_id = location.hash.split('/')[1]
	jQuery.support.cors=true
	$.ajax({
		url:get_portrait(),
		data:{
			id:doc_id
		},
		datatype:'JSON',
		success:function(data){
			//以下为警告的数据
			var risk = data.risk
			var contant_alert = $('.contant_alert');
			if(risk.status == 200){
				contant_alert.empty();
				contant_alert.css({'display':'block'})
				var str_start = `
					<div class="contant_alert_box">
						<img src="./image/risk.png" alt=""><br>
						<span>风险</span>
					</div>
					<div class="contant_conflict_box">`;
				var str_text = ``;
				var risk_data = risk.data.risk_list_pair;
				var index = 0;
				for(var i = 0;i<risk_data.length;i++){
					if(risk_data[i].length>0){
						if(risk.data.cols[i] == '药品相互作用'){
							str_text += `<p class="contant_alert_conflict"><span>三黄片</span>与<span>孕妇</span>存在药物相互作用</p>`
						}else{
							str_text += `<p class="contant_alert_conflict"><span>三黄片</span>与<span>孕妇</span>存在冲突</p>`
						}
						index++;
					}
				}
				var str_end = `
					</div>
					<p class="contant_alert_risk">我已充分了解风险</p>`
				contant_alert.append(str_start + str_text + str_end)
				switch(index){
					case 1:
						alert_style(40)
						break;
					case 2:
						alert_style(30)
						break;
					case 3:
						alert_style(20)
						break;
					case 4:
						alert_style(5)
						break;
				}
			}else{
				contant_alert.css({'display':'none'})
			}
			//以下为患者画像的数据
			var age = data.user_info.age_year[0];
			var sex = data.user_info.sex_code[0];
			var sex_str = sex_flag(sex);
			var str = ''
			if(age<=15){
				str = sex_str + '_child.png'
			}else if(age <=55){
				str = sex_str + '_middle.png'
			}else{
				str = sex_str + '_old.png'
			}
			var obj = {
				img:str,//对应的性别照片
				age:age,//对应的年龄
				retrieve_date:data.user_info.retrieve_date,//上一次体检
				diag_count:data.user_info.diag_count[0],//问诊次数
				report_title:data.user_info.report_title,//近一年检查项目
				effective_dtime:data.user_info.effective_dtime,//与上面对应的时间
				outpat_diag_name:data.user_info.outpat_diag_name,//既往病史
				drug_name:data.user_info.drug_name//历史用药
			};
			load(dom,obj)
		},
		error:function(){
			alert('错误')
		}
	})
}
// 此处为警告样式
function alert_style(num){
	$('.contant_conflict_box').css({'padding-top':num + 'px'})
}
function sex_flag(sex){ //性别和年龄
	return sex == 1 ? 'male':'female';
}
function medical_one_year(title,time){
	var str = '';
	for(var i = 0;i<title.length;i++){
		str +=`
			<tr>
				<td>${title[i]}</td>
				<td>${time[i]}</td>
			</tr>
		`
	}
	return str;
}
function medicine_history(name){
	var str = '';
	for(var i = 0;i<name.length;i++){
		str += `<a href="#">${name[i].name}</a>`
	}
	return str
}
function disease_history(name){
	var str = '';
	for(var i = 0;i<name.length;i++){
		str += `
			<p class="p${i}">${name[i]}</p>
		`
	}
	return str
}
function load(dom,obj){
	dom.empty();
	dom.append(`
		<div class="contant_pic_history">
			<div class="contant_pic_box">
				<div class="pic_box_in">
					${disease_history(obj.outpat_diag_name)}
				</div>
			</div>
			<div class="pic_white_box"></div>
			<div class="pic_title">
				<span>既往病史</span>
			</div>
		</div>
		<div class="contant_pic_number">
			<div class="pic_num_box">
				<p>这是他近一年第<span>${obj.diag_count}</span>次问诊</p>
			</div>
		</div>
		<div class="contant_pic_medicine">
			<div class="contant_pic_box">
				<div class="pic_box_in">
					<div class="wrapper">
					    <div class="tagcloud fl">
							${medicine_history(obj.drug_name)}
						</div>
					</div>
				</div>
			</div>
			<div class="pic_white_box"></div>
			<div class="pic_title">
				<span>历史用药</span>
			</div>
		</div>
		<div class="contant_pic_detection">
			<div class="contant_pic_box">
				<div class="pic_box_in">
					<table border=0 cellspacing=0>
						<thead>
							<tr>
								<th>检查检验项目</th>
								<th>检查检验时间</th>
							</tr>
						</thead>
						<tbody>
							${medical_one_year(obj.report_title,obj.effective_dtime)}
						</tbody>
					</table>
				</div>
			</div>
			<div class="pic_white_box"></div>
			<div class="pic_title">
				<span>近一年检查检验</span>
			</div>
		</div>
		<div class="contant_pic_last">
			<div class="pic_num_box">
				<p>他上一次在本院体检是<span>${obj.retrieve_date}</span>前</p>
			</div>
		</div>
		<div class="contant_pic_image">
			<img class="pic_image_img" src="./image/male_middle.png" alt="">
			<p class="pic_image_age">${obj.age}岁</p>
		</div>
	`)
	tagcloud({
	    selector: ".tagcloud",  //元素选择器
	    fontsize: 16,       //基本字体大小, 单位px
	    radius: 75,         //滚动半径, 单位px
	    mspeed: "normal",   //滚动最大速度, 取值: slow, normal(默认), fast
	    ispeed: "slow",   //滚动初速度, 取值: slow, normal(默认), fast
	    direction: 135,     //初始滚动方向, 取值角度(顺时针360): 0对应top, 90对应left, 135对应right-bottom(默认)...
	    keep: false          //鼠标移出组件后是否继续随鼠标滚动, 取值: false, true(默认) 对应 减速至初速度滚动, 随鼠标滚动
	});
	for(var i = 0;i<obj.outpat_diag_name.length;i++){
		var elem = '.p'+i;
		if(i>0){
			$(elem).css({'margin-left':Math.random()*100})
		}
	}
}