import {gate} from './rules/index.js'
import {get_rules} from './api/api.js'
export function rules(){
	var data_all = null;
	jQuery.support.cors=true
	$.ajax({
		url:get_rules(),
		data:{
			title:1 ,
		},
		datatype:'JSON',
		success:function(data){
			data_all = data;
			var dom = $('.contant_rules');
			dom.empty()
			var str = '';
			var str_end = `</div>`;
			var str_medicine_start = 			//药品使用限定
			`		
				<div class="contant_rules_box">
					<p>药品使用限定:</p>
			`
			var str_taboo_start = //药品禁忌
			`
				<div class="contant_rules_box">
					<p>药物使用禁忌:</p>
			`
			var str_collocation_start = //药物搭配
			`
				<div class="contant_rules_box">
					<p>药物搭配:</p>
			`
			var str_treatment_start = //诊疗限制
			`
				<div class="contant_rules_box">
					<p>诊疗项目限制:</p>
			`
			for(var i = 0;i<data.length;i++){
				var num = Number(data[i].range);
				switch(num){
					case 1:
						str_medicine_start += `<div class="contant_rules_nomal" id="${i}">${data[i].name}</div>`
						break;
					case 2:
						str_taboo_start += `<div class="contant_rules_nomal" id="${i}">${data[i].name}</div>`
						break;
					case 3:
						str_treatment_start += `<div class="contant_rules_nomal" id="${i}">${data[i].name}</div>`
						break;
					case 4:
						str_collocation_start += `<div class="contant_rules_nomal" id="${i}">${data[i].name}</div>`
						break;
				}
			}
			str = 	str_medicine_start + str_end + 
					str_taboo_start + str_end + 
					str_collocation_start + str_end + 
					str_treatment_start + str_end
			dom.append(str)
			var list = $('.contant_rules_box');
			list.off('click').on('click','div',function(event){
				//此处为了兼容ie,ie中没有toElement
				var id = event.toElement != null? Number(event.toElement.id) : Number(event.currentTarget.id);
				if(id >= 0){
					gate(data_all[id],data_all)
				}
			})
		},
		error:function(){
			alert('错误')
		}
	})
}