import {
	lay_page
} from '../nicePage.js';
import {
	table4type1
} from './page_type1.js';
import {
	table4type2
} from './page_type2.js';
import {
	table4type3
} from './page_type3.js';
import {
	table4type4
} from './page_type4.js';
import {
	table4type5
} from './page_type5.js';
import {
	table4type6
} from './page_type6.js';
import {
	table4type7
} from './page_type7.js';
import {
	table4type8
} from './page_type8.js';
import {
	table4type9
} from './page_type9.js';
import {
	table4type10
} from './page_type10.js';
import {
	table4type11
} from './page_type11.js';
import {
	table4type12
} from './page_type12.js';
import {
	table4type13
} from './page_type13.js';
import {
	table4type14
} from './page_type14.js';
import {
	set_params,
	null2str
} from './global.js';
import {get_rules} from '../api/api.js'
var global_data = null;
var global_click_data = null;
var global_config_json = null;
var dom = $('.contant_rules');
var config_default = {
		page: 1,
		num_every_page: 10,
	};
export function gate(click_data, data_all) {
	global_data = data_all;
	config_default.type = click_data.type
	var config_json = {
		page : config_default.page,
		num_every_page : config_default.num_every_page,
		type : config_default.type
	}
	ajax(dom, click_data, data_all, config_json)
}
/**
 * 用于select切换时执行的函数
 */
export function change(a) {
	var config_json = {
		page : config_default.page,
		num_every_page : config_default.num_every_page,
		type : a.target.value
	}
	ajax(dom, global_data[Number(a.target.value) - 1], global_data, config_json)
}
/**
 * click_search
 * 用于医保规则查询的单独请求
 * 其中click_data_g是数据依赖项
 */
export function click_search() {
	var text = $('.contant_rules_clues input')
	for (var i = 0; i < text.length; i++) {
		global_config_json[global_click_data.paras[i]] = text[i].value;
	}
	global_config_json.page = 1;
	ajax(dom, global_click_data, global_data, global_config_json)
}

function ajax(dom, click_data, data_all, config_json) {
	global_config_json = config_json;
	global_click_data = click_data;
	jQuery.support.cors = true
	//测试时使用，正式时注释/////////////////////
	var url = 'http://localhost:3000/ddd'
	if(config_json.drug == '' || config_json.cure ==''){
		url = 'http://localhost:3000/eee'
	}
	///////////////////////////////////////
	$.ajax({
		// url:get_rules(),
		url: url,
		data: config_json,
		contentType: false, // jQuery不要去设置Content-Type请求头
		datatype: 'JSON',
		success: function(data) {
			var num = Number(click_data.type);
			if(!data.num){
				dom.empty()
				var str_start =
					`
					分类 : <select class="contant_rules_input" name="" id="">
						<option value="${click_data.type}">${click_data.name}</option>`
				for (var i = 0; i < data_all.length; i++) {
					if (data_all[i].name != click_data.name) {
						str_start += `
								<option value="${data_all[i].type}">${data_all[i].name}</option>
							`
					}
				}
				str_start += `</select>`;
				var str_search = ``;
				var str_table = '';
				str_search = set_params(click_data.paras);
				for (var i = 0; i < data.info.length; i++) {
					null2str(data.info[i])
				}
				switch (num) {
					case 1:
						//药品限特定病种使用限制
						str_table = table4type1(data)
						break;
					case 2:
						//药品配伍关系查询
						str_table = table4type2(data)
						break;
					case 3:
						//药品限工伤或生育使用
						str_table = table4type3(data)
						break;
					case 4:
						//药品限特定性别使用
						str_table = table4type4(data)
						break;
					case 5:
						//药品仅限儿童使用
						str_table = table4type5(data)
						break;
					case 6:
						//药品限医院等级使用
						str_table = table4type6(data)
						break;
					case 7:
						//药品限中医师使用
						str_table = table4type7(data)
						break;
					case 8:
						//药品限复方使用
						str_table = table4type8(data)
						break;
					case 9:
						//儿童禁忌药品查询
						str_table = table4type9(data)
						break;
					case 10:
						//老人禁忌药品查询
						str_table = table4type10(data)
						break;
					case 11:
						//诊疗项目频次与疗效限制
						str_table = table4type11(data)
						break;
					case 12:
						//诊疗项目限儿童
						str_table = table4type12(data)
						break;
					case 13:
						//诊疗项目限时间
						str_table = table4type13(data)
						break;
					case 14:
						//诊疗项目限性别
						str_table = table4type14(data)
						break;
				}
				var str_end = `<div id="pageBar"></div>`
				dom.append(str_start + str_search + str_table + str_end)
				$('.contant_rules_clues').css({
					'width': (100 / click_data.paras.length) + '%'
				})
				$('.contant_rules_input').change(change)
				$('.contant_rules_clues input').change(function(e){
					console.log(1111)
					console.log(e)
				})
			}else{
				var tbody = $('.contant_rules_table tbody');
				if (data.code == 200) {
					tbody.empty();
					var str = ``
					for (var i = 0; i < data.info.length; i++) {
						str+= `<tr>`
						for(var j = 0;j<data.info[i].length;j++){
							if((data.type == 13 || data.type == 14) && j == 1){
								str += `<td>是</td>`
							}
							if(data.type == 13 && j == 1){
								str += `<td>${data.info[i][j]+data.info[i][j+1]}</td>`;
								break;
							}else{
								str += `<td>${data.info[i][j]}</td>`
							}
						}
						if (data.type == 1) {
							str += `<td>是</td>`
						}else if(data.type == 5){
							str += `<td>是</td>`
						}else if(data.type == 7){
							str += `<td>限中医师处方</td>`
						}else if(data.type == 8){
							str += `<td>限复方</td>`
						}else if(data.type == 12){
							str += `<td>是</td>`
						}
						str += `</tr>`
					}
					tbody.append(str)
				}	
			}
			if (data.code == 404) {
				tbody.empty();
				var str = ''
				str +=
					`<tr>
						<td colspan="3">未搜索到相关数据</td>
					</tr>`
				tbody.append(str)
			}
			nicePage.setCfg({
				table: 'table',
				bar: 'pageBar',
				limit: 10,
				color: '#1E9FFF',
				layout: ['count', 'prev', 'page', 'next', 'limit', 'skip'],
				jump: function(obj, first) {
					if (!first) {
						config_json.page = obj.curr;
						config_json.num_every_page = obj.limit;
						config_json.type = num;
						ajax(dom, click_data, data_all, config_json)
					}
				},
				count: data.num ? data.num : click_data.count
			});
			$('.contant_rules_search span').click(click_search)
			lay_page(config_json)
		},
		error: function() {
			alert('错误')
		}
	})
}
