export function table4type1(data){
	var str_table = '';
	var str_table_start = `
		<table class="contant_rules_table"  cellspacing=0>
			<thead>
				<tr>
					<td>药品</td>
					<td>病种</td>
					<td>是否可用</td>
				</tr>
			</thead>
			<tbody>`
	var table_mid = '';
	for(var i = 0;i<data.info.length;i++){
		table_mid += 
		`<tr>
			<td>${data.info[i][0]}</td>
			<td>${data.info[i][1]}</td>
			<td>是</td>
		</tr>`
	}
				
	var table_end = 	
			`</tbody>
		</table>
	`;
	str_table = str_table_start + table_mid + table_end;
	return str_table;
}