import {set_status} from './key/key_down.js'
export function choose(){
	var contant_card = $('.contant_card');
	contant_card.on('click','li',function(){
		$(this).attr('class','contant_card_list_active contant_card_list')
		$(this).siblings('.contant_card_list').attr('class','contant_card_list')
		var num = $(this).index();
		var status = '';
		switch(num){
			case 0:
				status = 'search'
				break;
			case 1:
				status = 'rules'
				break;
			case 2:
				status = 'portrait'
				break;
			case 3:
				//病例检索目前没做
				// status = ''
		}	
		set_status(status)
		var box = $('.contant_box');
		for(var i = 0;i<box.length;i++){
			var box_dis = $(box[i])
			if(i == num){
				box_dis.css({'display':'block'})
			}else{
				box_dis.css({'display':'none'})
			}
		}
	})
}