import {click} from '../index.js';
import {click_search} from '../rules/index.js'
var status = 'search';
export function key_down(){
	$(document).keydown(function(event){
		if(event.keyCode == 13 && status == 'search'){
			click(event)
		}
		if(event.keyCode == 13 && status == 'rules'){
			click_search()
		}
	})
}
export function set_status(value){
	status = value;
}